'use strict';

var React 				= require('react/addons');
var markEventAsHandled 	= require('./markEventAsHandled');

var keyHandlers = {
	38: 'handleUpKey',
	40: 'handleDownKey',
	32: 'handleSpaceKey',
	13: 'handleEnterKey',
	27: 'handleEscKey',
	74: 'handleDownKey',
	47: 'handleUpKey',
};

var KeyboardHandler  ={

	handleKeyDown(e){

		var handler = keyHandlers[e.which];

		if (handler && (typeof this[handler] === 'function')){

			if (handler === 'handleUpKey' || handler === 'handleDownKey' || handler === 'handleSpaceKey'){
				markEventAsHandled(e);
			}

			this[handler](e);

		} 
	},

	onKeyDown(e){
		if (this.isCtrlKeyHeldDown(e)){
			this.checkAndCall('onPressKeyWithCtrl', e);
		}
		else if (e.key !== 'Unidentified' && e.key !== ' '){
			var callBack = 'handle' + e.key + 'Key';
			this.checkAndCall(callBack, e);
		}
		else if (this.isKeyPrintable(e.keyCode)){
			this.checkAndCall('handleCharKey', e);
		}

	},

	isKeyPrintable(keycode){
		var valid = 
			(keycode > 47 && keycode < 58) 		|| // number keys
			(keycode === 32 || keycode === 13)	|| // spacebar & return
			(keycode > 64 && keycode < 91)		|| // letter keys
			(keycode > 95 && keycode < 112)		|| // numpad keys
			(keycode > 185 && keycode < 193)	|| // ;=,-./`
			(keycode > 218 && keycode < 223);	   // [\]'

		return valid;
	},

	isCtrlKeyHeldDown(e){
		return e.ctrlKey === true && e.key !== 'Control';
	},

	checkAndCall(methodName, args){
		if (typeof this[methodName] === 'function'){
			this[methodName](args);
		}
	}


};

module.exports = KeyboardHandler;


