'use strict';

var React         = require('react');
var PropTypes     = React.PropTypes;
var emptyFunction = require('./emptyFunction');

var Draggable = React.createClass({

  propTypes: {
    onDragStart: PropTypes.func,
    onDragEnd: PropTypes.func,
    onDrag: PropTypes.func,
    component: PropTypes.oneOfType([PropTypes.func, PropTypes.constructor])
  },

  render() {
    var {component:Component, ...props} = this.props;

    if (Component){
      return <Component onMouseDown={this.handleMouseDown} {...props}/>;
    }
    else {
      return <div onMouseDown={this.handleMouseDown} {...props}/>;
    }
  },

  getDefaultProps() {
    return {
      onDragStart: emptyFunction.thatReturnsTrue,
      onDragEnd: emptyFunction,
      onDrag: emptyFunction
    };
  },

  getInitialState() {
    return {
      drag: null
    };
  },

  handleMouseDown(e) {
    var drag = this.props.onDragStart(e);

    if (drag === null && e.button !== 0) {
      return;
    }

    this.x = e.clientX;
    this.y = e.clientY;

    window.addEventListener('mouseup', this.onMouseUp);
    window.addEventListener('mousemove', this.onMouseMove);

    this.setState({drag});
    
  },

  onMouseMove(e) {
    if (this.state.drag === null) {
      return;
    }

    if (e.stopPropagation) {
      e.stopPropagation();
    }

    if (e.preventDefault) {
      e.preventDefault();
    }

    var x = e.clientX;
    var y = e.clientY;

    var deltaX = x - this.x;
    var deltaY = y - this.y;

    this.x = x;
    this.y = y;

    this.props.onDrag(e, deltaX, deltaY);
  },

  onMouseUp(e) {
    this.cleanUp();
    this.props.onDragEnd(e, this.state.drag);
    this.setState({drag: null});
  },

  componentWillUnmount() {
    this.cleanUp();
  },

  cleanUp() {
    window.removeEventListener('mouseup', this.onMouseUp);
    window.removeEventListener('mousemove', this.onMouseMove);
  }
});

module.exports = Draggable;



