var React 				= require('react/addons')
var PropTypes			= React.PropTypes;
var cx					= React.addons.classSet;
var markEventAsHandled	= require('react-utils').markEventAsHandled;
var KeyboardHandler		= require('react-utils').KeyboardHandler;
var indexOf				= require('react-utils').indexOf;
var Popup				= require('react-services').Popup;


var ListItem = React.createClass({

	render(){
		
		var {className, data, idx, onMouseDown} = this.props;

		return (
			<li className={className} key={idx} data-key={data.value} 
				onClick={this.handleClick} 
				onMouseDown={onMouseDown}>{data.label}</li>
		);
	},

	handleClick(e){
		markEventAsHandled(e);
		this.props.onClick(this.props.idx, this.props.data.value)
	}

});

var SelectBox = React.createClass({

	mixins : [KeyboardHandler],

	propTypes : {
		onChange	: PropTypes.func.isRequired,
		componentClassName : PropTypes.string,
		optionListClassName : PropTypes.string,
		name 		: PropTypes.string, // recommended, will be returned in the onChange, with the new value(s)
		label		: PropTypes.string,
		values 		: PropTypes.array,
		multiple 	: PropTypes.bool,
		showClearButton : PropTypes.bool
	},

	render(){

		var {values, label, multiple} = this.props;
		var included = this.state.selected.map(this.getLabel);
		var hasValue = included.length && (multiple !== true || included.length < values.length); 

		var showClearButton = hasValue && this.props.showClearButton !== false;

		var className = cx(
			this.props.componentClassName,
			this.props.className,
			this.state.open ? 'open' : null,
			multiple ? 'multi' : null,
			!hasValue ? 'empty' : null
		);

		console.log('render SelectBox, focusedIndex = ' + this.state.focusedIndex);

		var content = hasValue ? included.join(', ') : label;
		
		var labelClassName = this.props.componentClassName + '-label';

		return (
			<div className={className}
				data-value={content}
				onClick={this.handleOpen}
				onKeyDown={this.handleKeyDown}
				onBlur={this.handleClose}
				tabIndex="0">
				<div className={labelClassName}>{content}</div>
				{showClearButton ? <button className="SelectBox-clear" onClick={this.handleClear} tabIndex="0"/> : null}
				{this.renderOptionMenu()}
			</div>

		);
	},

	renderOptionMenu(){
		if (this.state.open){

			var selected = this.state.selected;
			var {multiple, closeText} = this.props;
			var showCloseButton = multiple && closeText;

			var options = this.props.values.map((value, idx) => {
				var v = valueOf(value);
				var option = {label:labelFor(value), value:v, include:selected.indexOf(v) !== -1}
				var isFocused = idx === this.state.focusedIndex;

				var className = cx(
					'SelectBox-option',
					isFocused ? 'focused' : null,
					option.include ? 'selected' : null
				);

				return 	<ListItem className={className} key={idx} idx={idx} data={option} onClick={this.handleChange} onMouseDown={this.handleMouseDown}/>;
				
			});

			var className = cx("SelectBox-options",	this.props.optionListClassName, multiple ? 'multi' : null);

			return (
				<Popup position="below">	
					<div className={className}>
						<ul className="SelectBox-options-list">
							{options}
						</ul>
						{showCloseButton ? <button onClick={this.handleClose} className="SelectBox-close">{closeText}</button> : null}
					</div>
				</Popup>
			);
		}
		else {
			return <Popup close={true}/>;
		}
	},

	getDefaultProps(){
		return {
			componentClassName : 'SelectBox',
			values : [],
			closeText : 'Close'

		};
	},

	getInitialState(){

		var selected = Array.isArray(this.props.selected) ? this.props.selected :
					   this.props.selected !== undefined ? [this.props.selected] : [];

		return {
			open: false,
			focusedIndex: -1,
			pendingValue: [],
			selected
		};
	},

	componentWillReceiveProps(nextProps){

		if (nextProps.selected !== this.state.selected){
			var selected = Array.isArray(nextProps.selected) ? nextProps.selected :
				nextProps.selected !== undefined ? [nextProps.selected] : [];

			this.setState({selected});
		}
	},

	clickingOption: false,

	handleMouseDown(){
		this.clickingOption = true;
	},

	handleChange(idx, value){
		this.clickingOption = false;
		this.selectValue(idx, value);
	},

	selectValue(idx, value){

		if (this.props.multiple){

			var selected;
			if (value != null){
				selected = this.state.selected;
				var pos = selected.indexOf(value);
				if (pos !== -1){
					selected.splice(pos,1);
				}
				else {
					selected.push(value);
				}
			}
			else {
				selected = [];
			}

			this.props.onChange(selected, this.props.name, idx);

		}
		else {
			
			this.props.onChange(value, this.props.name, idx);

			this.handleClose();
		}

	},

	handleClear(e){
		markEventAsHandled(e);
		this.selectValue(null);
	},

	handleOpen(e){
		markEventAsHandled(e);
		this.setState({open:true});
	},

	handleClose(e){
		e && markEventAsHandled(e);

		if (!this.clickingOption){
			this.setState({open:false, focusedIndex: -1});
		}
	},

	moveFocus(move){
		var len = this.props.values.length;
		var idx = (this.state.focusedIndex + move + len) % len;
		this.setState({focusedIndex: idx});
	},

	handleUpKey(){
		this.moveFocus(-1);
	},

	handleDownKey(){

		if (!this.state.open){
			this.handleOpen();
		}
		this.moveFocus(1);
	},

	handleSpaceKey(){

		if (!this.state.open){
			this.handleOpen();
		}
		else if (this.state.focusedIndex !== -1){
			this.handleEnterKey();
		}
	},

	handleEnterKey(){
		var idx = this.state.focusedIndex; 
		if (idx !== -1){
			this.selectValue(idx, valueOf(this.props.values[idx]));
		}
	},

	handleEscKey(){

		if (this.state.open){
			this.handleClose();
		}
		else {
			this.handleClear();
		}

	},

	getLabel(value){
		var values = this.props.values;

		var idx = indexOf(values, v => valueOf(v) === value);

		if (idx !== -1){
			return labelFor(values[idx])
		}

	}

});

module.exports = SelectBox;

function valueOf(option){
	if (option != null){
		return typeof option === 'object' ? option.value : option;
	}
}

function labelFor(option){
	if (option != null){
		return option.label || option;
	}
}


